body {
    background-repeat: no-repeat;
}

#particles-js {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.background--top {
    position: relative;
    z-index: 0;
    height: 350px;

    &:before {
        content: "";
        z-index: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        opacity: 0.5;
        background-image: linear-gradient(85deg, var(--color-bg), transparent);
    }

    &:after {
        content: "";
        z-index: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        top: auto;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 1;
        background-image: linear-gradient(0deg, var(--color-bg) 12%, transparent 100%);
    }

    img {
        object-position: center;
        object-fit: cover;
        background-size: cover;
        width: 100%;
        height: 350px;
    }

    @include media-breakpoint-down(sm) {
        #serveur--ip {
            min-height: 0;
        }
    }

    #particles-js {
        z-index: 1;
    }
}

#preloader {
    z-index: 9999999;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-bg);
    flex-direction: column;

    img {
        max-width: 350px;
    }

    .spinner-border {
        margin-top: 4rem;
        height: 60px;
        width: 60px;
        border-color: var(--color-contrast-high);
        border-right-color: transparent;
    }
}
