#vote {
    #vote-spinner {
        background-color: var(--color-contrast-lower);
    }

    h2 {
        border-bottom: 3px solid var(--color-primary);
    }

    .podium {
        margin: 35px auto;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: flex-end;
        align-content: center;

        .player {
            text-align: center;

            .pseudo {
                font-size: 18px;
                margin-bottom: 10px;
                font-weight: bold;
            }

            img {
                height: 190px;
                width: auto;
            }

            .step {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 180px;
                background-color: var(--color-contrast-low);
                color: var(--color-contrast-higher);
                font-size: 20px;
                text-transform: uppercase;
                font-weight: bold;
                border-radius: $border-radius-lg;
                margin-top:0.2rem;

                i {
                    font-size: 25px;
                    margin-right: 8px;
                }
            }
        }

        .first-player {
            span {
                color: var(--color-success);
            }

            i {
                color: var(--color-success);
            }

            img {
                height: 190px;
            }

            .step {
                font-size: 32px;
                height: 75px;
                margin-right: 1rem;
                margin-left: 1rem;

                i {
                    font-size: 25px;
                }
            }
        }

        .second-player {
            span {
                color: var(--color-error);
            }

            i {
                color: var(--color-error);
            }

            img {
                height: 160px;
            }

            .step {
                font-size: 25px;
                height: 55px;

                i {
                    font-size: 20px;
                }
            }
        }

        .third-player {
            span {
                color: var(--color-warning);
            }

            i {
                color: var(--color-warning);
            }

            img {
                height: 140px;
            }

            .step {
                font-size: 18px;
                height: 40px;

                i {
                    font-size: 18px;
                }
            }
        }
    }

    @include media-breakpoint-down(md) {
        .podium {
            .player {
                flex-basis: 100%;
                margin: 0;
                margin-bottom: 15px;

                .pseudo {
                    font-size: 18px;
                }

                img {
                    height: 150px !important;
                }

                .step {
                    width: 200px;
                    padding: 6px 40px;
                    font-size: 18px;
                    margin: auto;
                }
            }

            .first-player {
                order: 1;
            }

            .second-player {
                order: 2;
            }

            .third-player {
                order: 3;
            }
        }
    }

}
