.footer {
    margin-top: 8rem;

    a {
        color: var(--color-contrast-high);

        &:focus,
        &:hover {
            color: var(--color-contrast-higher);
        }
    }

    &--logo {
        max-width: 300px;
        max-height: 170px;
        opacity: 0.3;
    }

    &--liens {
        .navbar-nav {
            flex-wrap: wrap;
            max-height: 300px;

            .nav-item {
                max-width: 50%;
            }
        }
    }

    &--links {
        &-item {
            font-size: 32px;
            color: var(--color-contrast-high);
            padding: 0 0.5rem;

            a {
                padding: 0 1rem;
            }
        }
    }

    .footer--top {
        padding: 3rem 0;
        background-color: var(--color-contrast-lower);
        border-top: 4px solid var(--color-primary);
        border-bottom: 1px dashed var(--color-primary);
    }

    .footer--bottom {
        padding: 1.5rem 0;
        background-color: var(--color-bg);
    }
}
