.glide {
    .glide__arrows {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        transform: translateY(-50%);
        width: 95vw;
        margin: 0 auto;

        .glide__arrow {
            background-color: transparent;
            border: 0;
            box-shadow: none;
            position: absolute;
            top: 0;
            color: var(--color-contrast-higher);
            font-size: 2rem;
            text-shadow: 3px 3px 4px var(--color-contrast-low);

            &--left {
                left: 0;
            }

            &--right {
                left: auto;
                right: 0;
            }
        }
    }
}
