#wiki {
    //@include card-bootstrap();

    a {
        color: var(--color-white);

        &:focus,
        &:hover {
            i {
                color: var(--color-white);
            }
        }
    }

    .card-body {
        border-radius: $border-radius-lg;
        transition: background-color 0.3s linear;
        background-color: var(--color-contrast-low);

        &:hover {
            background-color: var(--color-contrast-low);
        }
    }
}
