.home--top {
    position: relative;
    overflow: hidden;
    min-height: 350px;

    &.not-slider {
        min-height: 15rem;
    }
}

.background--top-header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    img {
        width: 100%;
        height: 100%;
        background-size: cover;
        object-fit: cover;
    }

    &:after {
        content: "";
        z-index: 10;
        position: absolute;
        height: 100%;
        width: 100%;
        top: auto;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 1;
        background-image: linear-gradient(0deg, var(--color-bg) 8%, transparent 100%);
        pointer-events: none;
    }

}

#particles-js {
    pointer-events: none;
}

.home--glide {
    position: relative;

    .glide__slides {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 10vh;

        .glide__slide {
            position: relative;

            img {
                object-fit: contain;
                max-height: 350px;
                max-width: 500px;
            }
        }

        .row {
            position: relative;
            z-index: 1;
        }

        .title {
            font-size: 2.2rem;
            font-weight: $font-weight-bold;
            text-shadow: 3px 3px 4px var(--color-contrast-low);
            margin-bottom: 0;
        }

        .text {
            font-size: 1.3rem;
            text-shadow: 3px 3px 4px var(--color-contrast-low);
        }
    }
}

.home--post {
    .post-preview {
        border: 0;
        border-radius: $border-radius-lg;
        box-shadow: $box-shadow;
        background-color: var(--color-contrast-lower);

        img {
            max-height: 250px;
            object-fit: cover;
            border-top-left-radius: $border-radius-lg;
            border-top-right-radius: $border-radius-lg;
        }

        .card-header {
            border-top: 4px solid var(--color-primary);
            border-bottom: 1px dashed var(--color-primary);
            background-color: var(--color-contrast-low);

            h3 {
                margin-bottom: 0;
                //todo
                color: var(--color-white) !important;
            }

            small {
                &:before {
                    content: '- ';
                }

                color: var(--color-contrast-high);
            }
        }

        .card-body {
            border: 0;
            background-color: var(--color-contrast-lower);
            border-bottom-left-radius: $border-radius-lg;
            border-bottom-right-radius: $border-radius-lg;
        }
    }
}

.home--info {
    iframe {
        padding: 0;
        width: 100% !important;
        box-shadow: $box-shadow;
    }

    .card-body {
        background-color: var(--color-contrast-low);
        border-radius: $border-radius-lg;
        margin-bottom: 2rem;
        box-shadow: $box-shadow;
    }
}

@include media-breakpoint-up(xs) {
    .home--top {
        min-height: 40vh;
    }
    .home--glide {
        .glide__slides {
            min-height: 40vh;
        }
    }
}

@include media-breakpoint-up(lg) {
    .home--top {
        min-height: 80vh;

        #serveur--ip {
            margin-top: -170px;
        }
    }

    .home--glide {
        .glide__slides {
            height: 80vh;

            .glide__slide {
                object-fit: none;

                &:before {
                    content: "";
                    z-index: 0;
                    position: absolute;
                    height: 100%;
                    width: 100%;
                    top: auto;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    opacity: 1;
                    background-image: linear-gradient(0deg, var(--color-bg) 8%, transparent 100%);
                    pointer-events: none;
                }
            }
        }
    }
    //.btn-wrapper-home {
    //    margin-top: -170px;
    //}
    //#serveur--ip {
    //    margin-top: -170px;
    //}
}

@include media-breakpoint-down(md) {

    .home--glide {
        .glide__slides {
            .glide__slide {
                background: none !important;
            }
        }
    }
}
