.pagination {

    .page-link {
        background-color: var(--color-contrast-lower);
        color: var(--color-contrast-high);
        border-color: var(--color-contrast-lower);
    }

    .disabled {
        .page-link {
            background-color: alpha(var(--color-contrast-lower), 50%);
        }
    }

    .active {
        .page-link {
            background-color: var(--color-contrast-low);
        }
    }
}
