form {
    label {
        color: var(--color-contrast-high);
    }

    textarea,
    .CodeMirror,
    .editor-toolbar,
    .custom-select,
    .custom-file-input,
    .custom-file-label,
    .form-control {
        color: var(--color-contrast-higher);
        background-color: var(--color-contrast-low);
        border-color: var(--color-contrast-medium);

        &:-webkit-autofill,
        &:visited,
        &:active,
        &:focus {
            color: var(--color-contrast-higher);
            background-color: var(--color-contrast-low);
            border-color: var(--color-bg);
        }
    }

    .editor-toolbar {
        button {
            color: var(--color-contrast-higher);
            &:hover,
            &:focus{
            color: var(--color-bg);
            }
        }
    }
}
