.server--ip-top {
    position: absolute;
    width: 100%;
    top: 9rem;
    bottom: auto;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
}

@include media-breakpoint-down(sm) {
    #serveur--ip {
        min-height: 350px;
    }
}

#serveur--ip {
    display: flex;
    align-items: end;

    .icon {
        font-size: 2.5rem;
        padding: 0 0.5rem;
    }

    .countRegister,
    .countConnectServer {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: row;
        flex-wrap: nowrap;
        padding: 0.5rem;
        border-radius: $border-radius-lg;
        background-image: linear-gradient(180deg, hsla(var(--color-contrast-lower-h), var(--color-contrast-lower-s), var(--color-contrast-lower-l), 0.8) 100%, transparent 100%);

    }

    .countConnectServer {
        cursor: pointer;
        text-align: right;
    }

    .countRegister {
        display: flex;
        justify-content: flex-start;
    }

    p {
        margin: 0;
    }

    .text {
        font-size: 0.875rem;
    }

    h2 {
        font-size: 1.3rem;
    }
}
