$height-link: 65px;
.header {
    position: fixed;
    width: 100%;
    z-index: 10;


    .navbar-collapse {

        &.collapsed {
            left: -75%;
            transition: height 0s ease;
        }

        &.show {
            left: 0;
            transition: left 300ms ease-in-out;
        }
    }


    .navbar-toggler {
        color: var(--color-contrast-higher);
        border: none;
        background: transparent !important;
        position: relative;
        float: right;
        padding: 9px 10px;
        margin: 8px auto 8px 0;
        background-image: none;
        border-radius: 0;

        &:hover {
            background: transparent !important;
        }

        .icon-bar {
            display: block;
            width: 22px;
            height: 2px;
            background-color: var(--color-contrast-higher);
            transition: all 0.2s;
            display: block;
            border-radius: 1px;

            & + .icon-bar {
                margin-top: 4px;
            }
        }

        &.collapsed ~ .navbar-collapse {
            transition: left 500ms ease-in-out;
        }

        &.x .icon-bar:nth-of-type(1) {
            transform: rotate(45deg);
            transform-origin: 10% 10%;
        }

        &.x .icon-bar:nth-of-type(2) {
            opacity: 0;
        }

        &.x .icon-bar:nth-of-type(3) {
            transform: rotate(-45deg);
            transform-origin: 10% 90%;
        }

        /* ANIMATED X COLLAPSED */
        &.x.collapsed .icon-bar:nth-of-type(1) {
            transform: rotate(0);
        }

        &.x.collapsed .icon-bar:nth-of-type(2) {
            opacity: 1;
        }

        &.x.collapsed .icon-bar:nth-of-type(3) {
            transform: rotate(0);
        }

    }


    .navbar {
        border-radius: 0;
        padding: 0 1rem;
        background-color: transparent;
        background-image: linear-gradient(180deg, hsla(var(--color-bg-h), var(--color-bg-s), var(--color-bg-l), 0.8) 100%, transparent 100%);
    }

    .navbar-brand {
        position: relative;
        max-width: 200px;
        width: 100%;
        margin-right: 0;

        img {
            margin-top: 2rem;
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translate(-50%, -50%);
            max-width: 150px;
        }
    }

    .nav-item {
        display: flex;
        align-items: center;


        &:hover,
        &:focus,
        &.active {
            .nav-link {
                color: var(--color-contrast-higher);

                &:before {
                    content: '';
                    position: absolute;
                    top: auto;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 4px;
                    background-color: var(--color-primary);
                }
            }
        }
    }

    .nav-link {
        position: relative;
        font-size: 16px;
        padding: 5px 0;
        overflow: hidden;
        text-transform: uppercase;
        font-weight: $font-weight-bolder;

        img {
            width: 35px;
            margin-right: 0.5rem;
        }
    }

    .icon-page,
    .icon-post {
        display: block;
        left: 0;
        position: absolute;
        text-align: center;
        top: 0;
        object-position: 0 -860px;
        background-size: 65px auto;
        background-repeat: no-repeat;
        margin-left: 0.5rem;

        ~ span {
            margin-left: 43px;
        }
    }

    .navbar-nav {
        a {
            display: flex;
            align-items: center;
            line-height: $height-link;
            height: $height-link;
        }

        .dropdown-item {
            line-height: initial;
            height: auto;
        }

        .dropdown-user {
            .dropdown-item {
                padding: 0.5rem 1rem;
                display: inline-block;
            }
        }
    }
}

@include media-breakpoint-down(lg) {
    .header {
        .navbar-brand {
            position: absolute;
            margin: 0.5rem 0;
            max-width: 100%;
            flex: 1 1 auto;
            width: 100vw;
            text-align: right;

            img {
                transform: none;
                max-height: $height-link;
                margin: auto 0 auto auto;
            }
        }

        .navbar {
            background-color: transparent;
            background-image: linear-gradient(180deg, hsla(var(--color-bg-h), var(--color-bg-s), var(--color-bg-l), 0.95) 100%, transparent 100%);
        }

        .navbar-expand-xl {
            flex-direction: row-reverse;
        }

        .navbar-collapse {
            background-image: linear-gradient(180deg, hsla(var(--color-bg-h), var(--color-bg-s), var(--color-bg-l), 0.95) 100%, transparent 100%);
            position: fixed;
            top: 48px;
            padding-left: 15px;
            padding-right: 15px;
            padding-bottom: 15px;
            width: 75vw;
            max-width: 300px;
            height: calc(100% - 48px);
            overflow: auto;

            .navbar {
                margin-top: 0;
            }

            .dropdown-menu {
                position: absolute;
            }

            a {
                display: inline-block;
                height: auto;
                line-height: initial;
                font-weight: $font-weight-bold;
                padding: 1rem 1rem;
                width: 100%;
            }
        }
    }
}
