.btn {
    padding: 0.5rem 1.5rem;
    border-radius: $border-radius-lg;
    transition: background-color 0.2s linear, border 0.2s linear, box-shadow 0.2s linear;

    &-primary {
        background-color: var(--color-primary);
        border-color: var(--color-primary);
        box-shadow: 2px 5px 9px 2px var(--color-bg);

        &:hover {
            background-color: var(--color-primary-dark);
            border-color: var(--color-primary-dark);
            box-shadow: 0px 0px 9px 2px var(--color-bg);
        }

        &:focus {
            background-color: var(--color-primary-darker);
            border-color: var(--color-primary-darker);
            box-shadow: 0px 0px 9px 2px var(--color-bg);
        }
    }

    &-download {
        text-align: center;
        position: relative;
        overflow: hidden;
        background-color: var(--color-download);
        border-color: var(--color-download);
        padding: 1.5rem 3rem 1.5rem 3rem;
        margin: 0 15px;
        text-transform: uppercase;
        font-weight: $font-weight-bold;
        //box-shadow: 0px 0px 9px 2px var(--color-download);


        img {
            display: block;
            left: 9px;
            position: absolute;
            text-align: center;
            top: 0;
            object-position: 0px -2689px;
            background-repeat: no-repeat;
        }

        &:hover {
            background-color: var(--color-download-dark);
            border-color: var(--color-download-dark);
            //box-shadow: 0px 0px 9px 2px var(--color-download);

        }

        &:focus {
            background-color: var(--color-download-darker);
            border-color: var(--color-download-darker);
            //box-shadow: 0px 0px 9px 2px var(--color-download);
        }
    }
}

.btn-wrapper {
    &-home {
        overflow: hidden;
        position: relative;
        top: auto;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        //padding: 2rem 0;
        //width: 100%;
        text-align: center;
    }

    &-footer {
        margin-top: 1rem;
        position: relative;
        overflow: hidden;
        padding: 3rem 0;
        width: 100%;
        text-align: center;
    }
}

@include media-breakpoint-down(md) {
    .btn-wrapper {
        &-home {
            margin-top: 0;
        }
    }
}
