@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@100;200;300;400;500;600;700;800;900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

h1,
h2,
h3,
h4,
h5,
h6 {
    &,
    a {
        font-family: $font-family-secondary;
        color: var(--color-contrast-higher);
    }
}

a, p {
    font-family: $font-family-base;
    color: var(--color-contrast-higher);
}

a {
    color: var(--color-contrast-higher);
    font-weight: $font-weight-bolder;
    text-decoration: none;
    cursor: pointer;
    transition: color 0.3s linear;
    &:hover{
        color: var(--color-contrast-high);
    }
}
