.banner {
    position: relative;
    background-color: var(--color-contrast-low);
    border-radius: $border-radius-lg;
    box-shadow: $box-shadow;
    //border-bottom: 3px solid var(--color-primary);
    margin: 0 0 1rem 0;
    padding: 9px 0;
    min-height: 66px;
    overflow: hidden;
    display: flex;
    align-items: center;

    &-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 66px;
        height: 100%;
        background-color: var(--color-primary);
        border-top-left-radius: $border-radius-lg;
        border-bottom-left-radius: $border-radius-lg;

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 66px;
            right: auto;
            transform: translateY(-50%);
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 5px 0 5px 10px;
            border-color: transparent transparent transparent var(--color-primary);
        }

        img {
            display: block;
            text-align: center;
            position: absolute;
            object-fit: cover;
            top: 50%;
            left: 50%;
            height: 55px;
            width: 50px;
            transform: translate(-50%, -50%);
            background-repeat: no-repeat;
            overflow: hidden;
        }
    }

    &-title {
        display: flex;
        align-items: center;
        width: 100%;
        padding-left: 85px;
        font-size: 26px;
        font-weight: $font-weight-bold;
        text-shadow: 3px 3px 4px var(--color-contrast-low);

        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin: 0;
        }

        &.no-image {
            padding-left: 1.5rem;
        }
    }
}
