:root, [data-theme="default"] {
    @include defineColorHSL(--color-primary-darker, 204, 70%, 33%);
    @include defineColorHSL(--color-primary-dark, 204, 70%, 43%);
    @include defineColorHSL(--color-primary, 204, 70%, 53%);
    @include defineColorHSL(--color-primary-light, 204, 70%, 63%);
    @include defineColorHSL(--color-primary-lighter, 204, 70%, 73%);

    @include defineColorHSL(--color-accent-darker, 282, 44%, 27%);
    @include defineColorHSL(--color-accent-dark, 282, 44%, 37%);
    @include defineColorHSL(--color-accent, 282, 44%, 47%);
    @include defineColorHSL(--color-accent-light, 282, 44%, 57%);
    @include defineColorHSL(--color-accent-lighter, 282, 44%, 67%);

    @include defineColorHSL(--color-download-darker, 145, 63%, 22%);
    @include defineColorHSL(--color-download-dark, 145, 63%, 32%);
    @include defineColorHSL(--color-download, 145, 63%, 42%);
    @include defineColorHSL(--color-download-light, 145, 63%, 52%);
    @include defineColorHSL(--color-download-lighter, 145, 63%, 62%);

    @include defineColorHSL(--color-black, 240, 8%, 12%);
    @include defineColorHSL(--color-white, 0, 0%, 100%);

    // feedback
    @include defineColorHSL(--color-warning-darker, 46, 68%, 29%);
    @include defineColorHSL(--color-warning-dark, 46, 68%, 39%);
    @include defineColorHSL(--color-warning, 46, 68%, 49%);
    @include defineColorHSL(--color-warning-light, 46, 68%, 59%);
    @include defineColorHSL(--color-warning-lighter, 46, 68%, 69%);

    @include defineColorHSL(--color-success-darker, 94, 48%, 20%);
    @include defineColorHSL(--color-success-dark, 94, 48%, 30%);
    @include defineColorHSL(--color-success, 94, 48%, 40%);
    @include defineColorHSL(--color-success-light, 94, 48%, 50%);
    @include defineColorHSL(--color-success-lighter, 94, 48%, 60%);

    @include defineColorHSL(--color-error-darker, 6, 63%, 26%);
    @include defineColorHSL(--color-error-dark, 6, 63%, 36%);
    @include defineColorHSL(--color-error, 6, 63%, 46%);
    @include defineColorHSL(--color-error-light, 6, 63%, 56%);
    @include defineColorHSL(--color-error-lighter, 6, 63%, 66%);

    @include defineColorHSL(--color-info-darker, 204, 62%, 27%);
    @include defineColorHSL(--color-info-dark, 204, 62%, 37%);
    @include defineColorHSL(--color-info, 204, 62%, 47%);
    @include defineColorHSL(--color-info-light, 204, 62%, 57%);
    @include defineColorHSL(--color-info-lighter, 204, 62%, 67%);

    // color contrasts
    @include defineColorHSL(--color-bg, 220, 5%, 22%);
    @include defineColorHSL(--color-contrast-lower, 216, 4%, 25%);
    @include defineColorHSL(--color-contrast-low, 220, 3%, 34%);
    @include defineColorHSL(--color-contrast-medium, 220, 2%, 63%);
    @include defineColorHSL(--color-contrast-high, 180, 2%, 90%);
    @include defineColorHSL(--color-contrast-higher, 0, 0%, 100%);
}

